import React, { useState, useEffect } from 'react';
import { mdiDatabase, mdiLoading } from '@mdi/js';
import { createUseStyles } from 'react-jss';
import classnames from 'classnames';
import Icon from '@mdi/react';

export default function Loading({ spinner = false, size = 2, spin = 1, className, ...props }) {
    const styles = createUseStyles({
        '@keyframes pulse': {
            '0%': { opacity: 1 },
            '50%': { opacity: 0.25 },
            '100%': { opacity: 1 },
        },
        database: {
            animation: '$pulse 1s infinite',
        },
    });
    const classes = styles();

    if (spinner) {
        return <Icon className={className} path={mdiLoading} size={size} spin={spin} {...props} />
    }

    return (
        <Icon className={classnames(classes.database, className)} path={mdiDatabase} size={size} {...props} />
    );
}
